@import "_variables";
@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900);
body {
  margin: 0;
  overflow-x: hidden;
  font-weight: 300;
  min-width: 240px;
}

#wrapper {
  width: 100%;
}

error-box {
  height: 100%;
  position: fixed;
  top: 20%;
  width: 100%;
}

.error-box .footer {
  width: 100%;
  left: 0;
  right: 0;
}

.error-body {
  padding-top: 5%;
}

.error-body h1 {
  font-family: Rubik;
  font-size: 210px;
  font-weight: 900;
  line-height: 210px;
}

.m-b-40 {
  margin-bottom: 40px !important;
}

.m-t-30 {
  margin-top: 30px !important;
}

.m-b-30 {
  margin-bottom: 30px !important;
}

@media only screen and (max-width: 520px) {
  .error-body h1 {
    font-size: 100px;
    font-weight: 700;
    line-height: 110px;
  }
}
